.product-container{
    width: 100%;
    padding: 40px;
    background-color: rgb(253, 253, 253);
}
.product-container h1{
    text-align: center;
    margin-bottom: 25px;
}
.cards-container{
    width: min(90%, 70.5rem);
    margin-inline: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 40px;

}
.card{
    height: 450px;
    width: 300px;
    background-color: rgb(253, 253, 253);
    box-shadow: 0 0 28px 4px rgba(0,0,0,0.1);
    
    
}
.card-content{
    padding: 10px;
}
