.card:hover{
    transform: scale(1.1);
    /* transition: 1s all ease; */
    transition: transform .3s ease;
}
.card-title{
    font-weight: 900;
    margin-bottom: 10px;
    
}
.card-img{
    width: 250px;
    display: grid;
    place-content: center;
    margin-bottom: 10px;
    margin-bottom: 60px;
    
}
.card-img img{
    width: 150px;
    object-fit: cover;
    
}
.card-price{
    font-weight: 800;
    margin-bottom: 5px;
    
}
