.about-container{
    display: flex;
}
.about-text{
    width: 45%;
    padding: 100px;
    margin-top: 2%;
}
.text1{
    font-size: 20px;
    font-weight: bolder;
    margin-bottom: 12px;
}
.text2{
    line-height: 25px;
    letter-spacing: 1.5px;
    font-style: italic;
    font-weight: 700;
    margin-bottom: 20px;
}
button{
    background-color: red;
    color: white;
    outline: 0;
    border: 0;
    width: 110px;
    height: 35px;
    border-radius: 5px;
    cursor: pointer;
}
.btn-text{
    font-weight: 800;
}
button:hover{
    background-color: black;
    color: red;
}
.about-img{
    background-image: url(./assets/about.png);
    background-position: center;
    background-size: 120%;
    background-repeat: no-repeat;
    height: 90vh;
    width: 55%;
}
.about-img img{
    width: 100%;
    /* margin-right: 30%; */
}