.contact-container{
    background-color: rgb(252, 220, 237);
    width: 100%;
    overflow: hidden;
}
.contact-header{
    /* margin: auto; */
    text-align: center;
    margin-top: 30px;
    margin-bottom: 30px;
}
.contact-detail{
    display: flex;
    justify-content: space-around;
}
.contact-address{
    display: block;
}
.location{
    width: 30%;
    height: 40px;
    padding: 8px;
    border-radius: 50%;
    margin-left: 70px;
}
.address p{
    text-align: center;
    color: red;
    font-weight: 800;
}
.head{
    font-weight: 800;
}
.address-content{
    margin-bottom: 15px;
}
.address-content p{
    text-align: center;
    letter-spacing: 1px;   
}
.contact-phone{
    display: block;
}
.phone-icon{
    width: 30%;
    height: 40px;
    border-radius: 50%;
    margin-left: 60px; 
}
.phone p{
    text-align: center;
    color: red;
    font-weight: 800;
}
.phone-content{
    margin-bottom: 15px;
}
.phone-content p{
    text-align: center;
    letter-spacing: 1px;
}
.contact-email{
    display: block;
    margin-top: 10px;
}
.email-icon{
    width: 30%;
    height: 40px;
    margin-left: 120px;
    margin-bottom: -15px;
}
.email p{
    text-align: center;
    color: red;
    font-weight: 800;
}
.email-content{
    margin-bottom: 15px;
}
.email-content p{
    text-align: center;
    letter-spacing: 1px;
}
.contact-detail2{
    width: 100%;
    display: flex;
}
.message{
    width: 50%;
    padding: 70px;
    margin-left: 20px;
    margin-top: 20px;
}
.message-textarea{
    font-weight: 600;
    line-height: 30px;
    letter-spacing: 1px;
}
.message-us{
    font-weight: 700;
    color: red;
    margin-bottom: 15px;
}
form {
    display: flex;
    flex-direction: column;
    padding: 20px;
}
form label{
    font-weight: 700;
}
form input{
    margin-bottom: 15px;
    width: 500px;
    height: 30px;
}
form textarea{
    height: 200px;
    margin-bottom: 25px;
}