*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.landing-container{
    display: flex;
    /* margin-left: 15px;
    margin-right: 20px; */
    background-color: rgb(252, 220, 237);
    width: 100%;
    height: 92vh;
    overflow: hidden;
}
.content{
    width: 55%;
    padding: 100px;
    margin-top: 8%;
}
.first-line{
    color: red;
    margin-bottom:6px;
}
.second-line{
    font-size: x-large;
    font-weight: 900;
    margin-bottom: 6px;
}
.third-line{
    line-height: 25px;
    margin-bottom: 20px;
    font-size: 18px;
}
.LandingImage{
    width: 45%;
    padding: 15px;
}
.LandingImage img{
    width: 100%;
    /* margin-right: 20px; */
}
button{
    background-color: red;
    color: white;
    outline: 0;
    border: 0;
    width: 110px;
    height: 35px;
    border-radius: 5px;
    cursor: pointer;
}
.btn-text{
    font-weight: 800;
}
button:hover{
    background-color: black;
    color: red;
}