.nav-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
}
.logo{
    color: black;
    padding: 10px 0 0 0;
    margin-left: 60px;
    font-size: 20px;
}
.logo a{
    text-decoration: none;
    color: black;
}
.nav-links{
    padding: 10px 0 0 0;
    margin-right: 60px;
}
.links{
    display: flex;
    list-style: none;
    gap: 30px;
}
.links li{
    margin: 0 1rem;
}
.links li a{
    color: black;
    text-decoration: none;
}
.links li a:hover{
    color: rgb(245, 185, 73);
}
.icons{
    padding: 10px 0 0 0;
    display: flex;
    gap: 20px;
    margin-right: 60px;
}